html,
body {
  padding: 0 !important;
  margin: 0 !important;
  box-sizing: border-box;
  max-width: 100% !important;
  overflow-x: hidden !important;
  overscroll-behavior: none;

  background: "#0d0d0d" !important;
}

::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #c7c7c7;
  opacity: 1; /* Firefox */
}

:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: red;
}

::-ms-input-placeholder {
  /* Microsoft Edge */
  color: red;
}

